@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  padding: 0;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

* {
  color: #000;
  color: var(--black);
  box-sizing: border-box;
  font-family: 'PT Sans', sans-serif;
}

:root {
  --black: #000;
  --white: #FFF;
  --main: #72BF44;
  --neutral: #BBBBBB;
  --featured: #E63F36;
  --secondary: #fae800;
}

button {
  cursor: pointer;
}
.products {
  width: 80%;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
}

.products__title {
  margin-bottom: 3rem;
}

.products__list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.products__list--item {
  flex: 0 0 20%;
  max-width: 20%;
  padding-left: 10px;
  position: relative;
  padding-right: 10px;
  margin-bottom: 20px;
}

@media (max-width: 970px) {
  .products {
    width: 90%;
  }

  .products__list--item {
    flex: 0 0 25%;
    max-width: 25%;
  }
}

@media (max-width: 768px) {
  .products {
    width: 95%;
  }

  .products__list--item {
    flex: 0 0 50%;
    max-width: 50%;
  }
}
.product {
  height: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
}

.product__tag {
  right: 0;
  top: 10px;
  padding: 5px 10px;
  font-size: 0.9rem;
  position: absolute;
  color: var(--white);
  background-color: var(--main);
}

.product__picture {
  width: 100%;
  object-fit: contain;
}

.product__offer-label {
  display: flex;
  padding: 5px 10px;
  justify-content: space-between;
  background-color: var(--featured);
}

.product__offer-label span {
  font-weight: bold;
  color: var(--secondary);
}

.product__info {
  display: flex;
  flex: 1 1 auto;
  padding: 10px 15px;
  flex-direction: column;
}

.product__info--name {
  overflow: hidden;
  font-size: 0.9rem;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.product__info--button {
  border: none;
  display: block;
  transition: .4s;
  font-weight: bold;
  font-size: 0.9rem;
  margin: 15px auto;
  padding: 10px 25px;
  border-radius: 20px;
  color: var(--white);
  background-color: var(--main);
}

.product__info--button:hover {
  box-shadow: 0 5px 10px rgba(0,0,0,0.3);
}

.product__info--price {
  display: flex;
  margin-top: auto;
  padding-bottom: 10px;
  flex-direction: column;
  border-bottom: 4px solid var(--main);
}

.product__info--price__old {
  text-decoration: line-through;
}

.product__info--price__old,
.product__info--price__installment {
  font-size: 0.75rem;
}

.product-in-offer .product__info--price {
  padding: 10px 20px;
  border-bottom-color: transparent;
  background-color: var(--secondary);
}

.product-in-offer .product__info--price__main {
  color: var(--featured);
}

@media (max-width: 768px) {
  .product {
    border: 1px solid var(--neutral);
  }
}
